import React from 'react'
import {
  Input,
  Button,
  FormGroup,
  UncontrolledDropdown,
  DropdownItem,
  DropdownMenu,
  DropdownToggle
} from 'reactstrap'
import Select from 'react-select'
import { X, Bookmark, Star, Tag, Check } from 'react-feather'
import Checkbox from '../../components/@vuexy/checkbox/CheckboxesVuexy'
import PerfectScrollbar from 'react-perfect-scrollbar'
import VideoRecorder from 'react-video-recorder'
import { Recorder } from 'react-voice-recorder'
import 'react-voice-recorder/dist/index.css'
import { connect } from 'react-redux'
import {
  starTask,
  completeTask,
  importantTask,
  updateTask,
  updateLabel,
  addNewTask
} from '../../redux/actions/diary/index'
const colourOptions1 = [
  {
    value: 'Text',
    label: 'Text',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Scribble',
    label: 'Scribble',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Attachment',
    label: 'Attachment',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Voice',
    label: 'Voice',
    color: '#0052CC',
    isFixed: true
  },
  {
    value: 'Video',
    label: 'Video',
    color: '#0052CC',
    isFixed: true
  }
]
class TaskSidebar extends React.Component {
  state = {
    audioDetails: {
      url: null,
      blob: null,
      chunks: null,
      duration: {
        h: 0,
        m: 0,
        s: 0
      }
    },
    ptype: '',
    taskToUpdate: null,
    taskTitle: '',
    taskDesc: '',
    taskStatus: false,
    taskStarred: false,
    taskImportant: false,
    newTask: {
      title: '',
      desc: '',
      tags: [],
      isCompleted: false,
      isImportant: false,
      isStarred: false
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (
      (this.props.taskToUpdate !== null &&
        this.state.taskToUpdate !== this.props.taskToUpdate) ||
      (this.props.taskToUpdate !== null &&
        this.state.taskStatus !== this.props.taskToUpdate.isCompleted) ||
      (this.props.taskToUpdate !== null &&
        this.state.taskStarred !== this.props.taskToUpdate.isStarred) ||
      (this.props.taskToUpdate !== null &&
        this.state.taskImportant !== this.props.taskToUpdate.isImportant)
    ) {
      this.setState({
        taskToUpdate: this.props.taskToUpdate,
        taskTitle: this.props.taskToUpdate.title,
        taskDesc: this.props.taskToUpdate.desc,
        taskStatus: this.props.taskToUpdate.isCompleted,
        taskStarred: this.props.taskToUpdate.isStarred,
        taskImportant: this.props.taskToUpdate.isImportant
      })
    } else {
      return
    }
  }
  handleAudioStop = (data) => {
    console.log(data)
    this.setState({ audioDetails: data })
  }
  handleAudioUpload = (file) => {
    console.log(file)
  }
  handleRest = () => {
    const reset = {
      url: null,
      blob: null,
      chunks: null,
      duration: {
        h: 0,
        m: 0,
        s: 0
      }
    }
    this.setState({ audioDetails: reset })
  }

  handleNewTaskTags = (tag) => {
    let tagsArr = this.state.newTask.tags
    if (tagsArr.includes(tag)) {
      tagsArr.splice(tagsArr.indexOf(tag), 1)
    } else {
      tagsArr.push(tag)
    }
    this.setState({
      ...this.state.newTask,
      tags: tag
    })
  }

  renderTags = (taskArr) => {
    return taskArr.map((tag, i) => (
      <div className="chip mb-0 mr-50" key={i}>
        <div className="chip-body">
          <span className="chip-text">
            <span
              className={`bullet bullet-${
                tag === 'professional'
                  ? 'warning'
                  : tag === 'others'
                  ? 'success'
                  : tag === 'todo'
                  ? 'danger'
                  : 'primary'
              } bullet-xs`}
            />
            <span className="text-capitalize ml-25">{tag}</span>
          </span>
        </div>
      </div>
    ))
  }

  render() {
    const { taskToUpdate, taskTitle, taskDesc, newTask, taskStatus } =
      this.state

    return (
      <div
        className={`task-sidebar ${
          this.props.addTaskState === true ? 'show' : ''
        }`}
      >
        <div className="task-header">
          <div className="d-flex justify-content-between">
            <div className="task-type-title text-bold-600">
              <h3 style={{ color: 'var(--warning)' }}>
                {this.props.taskToUpdate && this.props.taskToUpdate.id
                  ? 'Update Details'
                  : 'Enter Details'}
              </h3>
            </div>
            <div className="close-icon">
              <X
                className="cursor-pointer"
                size={20}
                onClick={() => {
                  this.props.addTask('close')
                }}
              />
            </div>
          </div>
        </div>

        <PerfectScrollbar>
          <div className="task-body">
            <div className="d-flex justify-content-between mb-2">
              <div className="mark-complete">
                {this.props.taskToUpdate && this.props.taskToUpdate.id && (
                  <Checkbox
                    color="primary"
                    className="user-checkbox"
                    icon={<Check className="vx-icon" size={15} />}
                    label={''}
                    checked={taskStatus}
                    onChange={(e) => {
                      this.props.completeTask(this.props.taskToUpdate)
                    }}
                  />
                )}
              </div>
              <div className="task-actions">
                <Star
                  size={20}
                  className={`mr-50 ${
                    (this.props.taskToUpdate !== null &&
                      this.state.taskImportant) ||
                    newTask.isImportant
                      ? 'text-success'
                      : ''
                  }`}
                  onClick={() => {
                    if (this.props.taskToUpdate !== null) {
                      this.props.importantTask(this.props.taskToUpdate)
                    } else {
                      this.setState({
                        newTask: {
                          ...this.state.newTask,
                          isImportant: !this.state.newTask.isImportant
                        }
                      })
                    }
                  }}
                />
                <Bookmark
                  size={20}
                  className={`mr-50 ${
                    (this.props.taskToUpdate !== null &&
                      this.state.taskStarred) ||
                    newTask.isStarred
                      ? 'text-warning'
                      : ''
                  }`}
                  onClick={() => {
                    if (this.props.taskToUpdate !== null) {
                      this.props.starTask(this.props.taskToUpdate)
                    } else {
                      this.setState({
                        newTask: {
                          ...this.state.newTask,
                          isStarred: !this.state.newTask.isStarred
                        }
                      })
                    }
                  }}
                />
                <UncontrolledDropdown className="d-inline-block">
                  <DropdownToggle tag="span">
                    <Tag className="mr-50" size={20} />
                  </DropdownToggle>
                  <DropdownMenu tag="ul" right>
                    <DropdownItem tag="li">
                      <Checkbox
                        color="primary"
                        className="user-checkbox"
                        icon={<Check className="vx-icon" size={12} />}
                        label={'Personal'}
                        checked={
                          (this.props.taskToUpdate !== null &&
                            this.props.taskToUpdate.tags.includes(
                              'personal'
                            )) ||
                          newTask.tags.includes('personal')
                            ? true
                            : false
                        }
                        size="sm"
                        onChange={(e) => {
                          e.stopPropagation()
                          if (this.props.taskToUpdate !== null)
                            this.props.updateLabel(taskToUpdate.id, 'personal')
                          else this.handleNewTaskTags('personal')
                        }}
                      />
                    </DropdownItem>
                    <DropdownItem tag="li">
                      <Checkbox
                        color="primary"
                        labelColor="#ff9f43"
                        className="user-checkbox"
                        icon={<Check className="vx-icon" size={12} />}
                        label={'Professional'}
                        checked={
                          (this.props.taskToUpdate !== null &&
                            this.props.taskToUpdate.tags.includes(
                              'professional'
                            )) ||
                          newTask.tags.includes('professional')
                            ? true
                            : false
                        }
                        size="sm"
                        onClick={(e) => e.stopPropagation()}
                        onChange={(e) => {
                          e.stopPropagation()
                          if (this.props.taskToUpdate !== null)
                            this.props.updateLabel(
                              taskToUpdate.id,
                              'professional'
                            )
                          else this.handleNewTaskTags('professional')
                        }}
                      />
                    </DropdownItem>
                    <DropdownItem tag="li">
                      <Checkbox
                        color="primary"
                        className="user-checkbox"
                        icon={<Check className="vx-icon" size={12} />}
                        label={'Others'}
                        labelColor="#25af63"
                        checked={
                          (this.props.taskToUpdate !== null &&
                            this.props.taskToUpdate.tags.includes('others')) ||
                          newTask.tags.includes('others')
                            ? true
                            : false
                        }
                        size="sm"
                        onClick={(e) => e.stopPropagation()}
                        onChange={(e) => {
                          e.stopPropagation()
                          if (this.props.taskToUpdate !== null)
                            this.props.updateLabel(taskToUpdate.id, 'others')
                          else this.handleNewTaskTags('others')
                        }}
                      />
                    </DropdownItem>
                    <DropdownItem tag="li">
                      <Checkbox
                        color="primary"
                        className="user-checkbox"
                        labelColor="#ea5455"
                        icon={<Check className="vx-icon" size={12} />}
                        label={'TO DO List'}
                        checked={
                          (this.props.taskToUpdate !== null &&
                            this.props.taskToUpdate.tags.includes('todo')) ||
                          newTask.tags.includes('todo')
                            ? true
                            : false
                        }
                        size="sm"
                        onClick={(e) => e.stopPropagation()}
                        onChange={(e) => {
                          e.stopPropagation()
                          if (this.props.taskToUpdate !== null)
                            this.props.updateLabel(taskToUpdate.id, 'todo')
                          else this.handleNewTaskTags('todo')
                        }}
                      />
                    </DropdownItem>
                  </DropdownMenu>
                </UncontrolledDropdown>
              </div>
            </div>

            <FormGroup className="form-label-group">
              <Select
                className="React"
                classNamePrefix="select"
                // defaultValue={colourOptions1[0]}

                ref={this.state.selectAssetRef}
                name="color"
                options={colourOptions1}
                isClearable={true}
                placeholder="Select Message Type..."
                onChange={(e) => {
                  this.setState({
                    ptype: e ? e.value : ''
                  })
                }}
              />
            </FormGroup>
            <FormGroup>
              <Input
                type="text"
                placeholder="Title"
                value={
                  this.props.taskToUpdate !== null ? taskTitle : newTask.title
                }
                onChange={(e) => {
                  if (this.props.taskToUpdate !== null) {
                    this.setState({
                      taskTitle: e.target.value
                    })
                  } else {
                    this.setState({
                      newTask: {
                        ...this.state.newTask,
                        title: e.target.value
                      }
                    })
                  }
                }}
              />
            </FormGroup>
            {this.state.ptype === 'Video' && (
              <VideoRecorder
                onRecordingComplete={(videoBlob) => {
                  // Do something with the video...
                  console.log('videoBlob', videoBlob)
                }}
              />
            )}
            {this.state.ptype === 'Voice' && (
              <Recorder
                record={true}
                title={'New recording'}
                audioURL={this.state.audioDetails.url}
                showUIAudio
                handleAudioStop={(data) => this.handleAudioStop(data)}
                handleAudioUpload={(data) => this.handleAudioUpload(data)}
                handleRest={() => this.handleRest()}
              />
            )}
            <FormGroup>
              <Input
                type="textarea"
                placeholder="Description"
                rows="5"
                value={
                  this.props.taskToUpdate !== null ? taskDesc : newTask.desc
                }
                onChange={(e) => {
                  if (this.props.taskToUpdate !== null) {
                    this.setState({
                      taskDesc: e.target.value
                    })
                  } else {
                    this.setState({
                      newTask: {
                        ...this.state.newTask,
                        desc: e.target.value
                      }
                    })
                  }
                }}
              />
            </FormGroup>
            <div className="chip-wrapper my-1 d-flex flex-wrap">
              {this.props.taskToUpdate !== null &&
              this.props.taskToUpdate.tags &&
              this.props.taskToUpdate.tags.length > 0
                ? this.renderTags(this.props.taskToUpdate.tags)
                : null}
            </div>
            <div
              className="d-flex justify-content-end"
              style={{ textAlign: 'right' }}
            >
              <Button.Ripple
                outline
                className="mr-1 mb-1 button-label"
                color="secondary"
                onClick={() => {
                  this.props.addTask('close')
                  this.props.handleUndoChanges()
                  this.setState({
                    newTask: {
                      title: '',
                      desc: '',
                      tags: [],
                      isCompleted: false,
                      isImportant: false,
                      isStarred: false
                    }
                  })
                }}
              >
                Cancel
              </Button.Ripple>{' '}
              <Button.Ripple
                className="mb-1 button-label"
                color="warning"
                onClick={() => {
                  console.log('taskToUpdate', taskToUpdate)
                  // toast.success('Note Added Successfully!!')

                  if (this.props.taskToUpdate !== null) {
                    const { isCompleted, isImportant, isStarred, tags } =
                      taskToUpdate
                    this.props.updateTask(
                      taskToUpdate._id,
                      taskTitle,
                      taskDesc,
                      taskToUpdate,
                      isCompleted,
                      isImportant,
                      isStarred,
                      tags
                    )
                  } else {
                    this.props.addNewTask(this.state.newTask)
                  }
                  this.props.addTask('close')
                  this.setState({
                    newTask: {
                      title: '',
                      desc: '',
                      tags: [],
                      isCompleted: false,
                      isImportant: false,
                      isStarred: false
                    }
                  })
                }}
                disabled={
                  taskTitle.length > 0 || newTask.title.length > 0
                    ? false
                    : true
                }
              >
                {taskToUpdate &&
                taskToUpdate.id &&
                this.props.taskToUpdate !== null
                  ? 'Update'
                  : 'Add'}
              </Button.Ripple>
            </div>
          </div>
        </PerfectScrollbar>
      </div>
    )
  }
}
const mapStateToProps = (state) => {
  return {
    app: state.todoApp
  }
}

export default connect(mapStateToProps, {
  completeTask,
  importantTask,
  starTask,
  updateTask,
  updateLabel,
  addNewTask
})(TaskSidebar)
